import Vue from 'vue'
import App from './App.vue'

/**************************************/
// 1.
import routerConfig from './routerConfig.js'

// 2.
import VueRouter from 'vue-router'

// 3.
Vue.use(VueRouter);

// 4.
const router = new VueRouter({
    /*#*/
    mode:'history',

    routes:routerConfig
});

/**************************************/
// axiosuse
import axios from 'axios'

//axiosVueaxios  this.$http
Vue.prototype.$http = axios

/**************************************/
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})


