/*  */
import Home from './components/Home.vue'
import Resume from './components/Resume.vue'

/*  */
export default [
    {
        path:'/home',
        component:Home
    },
    {
        path:'/myresume',
        component:Resume
    },
    {path:'*', redirect:'/home'}
]


