<template>
    <div id="mainBody">
        <!--  START -->
        <section class="container resume evaluation">
            <dl class="icon">
                <dt><i class="iconfont icon-mingpian"></i>{{homeData.basicInformation}}</dt>
                <dd><span></span>{{homeData.personalInformation}}</dd>
                <dd><span></span>{{homeData.graduatedSchool}}</dd>
                <dd><span></span>{{homeData.personalProfessional}}</dd>
                <dd><span></span>{{homeData.postInfo}}</dd>
                <dd><span></span>{{homeData.currAddress}}</dd>
                <dd><span></span>{{homeData.currSituation}}</dd>
            </dl>
            
            <dl class="icon">
                <dt><i class="iconfont icon-zuoji"></i>{{homeData.contactNumber}}</dt>
                <dd><span><i class="iconfont icon-shouji"></i></span>{{homeData.tel}}</dd>
                <dd><span><i class="iconfont icon-weixin"></i></span>{{homeData.wechat}}</dd>
                <dd><span><i class="iconfont icon-QQ"></i>&nbsp;&nbsp;QQ</span>{{homeData.QQ}}</dd>
                <dd><span><i class="iconfont icon-youjian"></i></span>{{homeData.email}}</dd>
                <dd><span><i class="iconfont icon-github"></i>Github&nbsp;&nbsp;&nbsp;&nbsp;</span><a href="https://github.com/yhfhappy" target="_balnk">{{homeData.Github}}</a></dd>
            </dl>
            
            <dl class="icon">
                <dt><i class="iconfont icon-zixuan"></i>{{homeData.mainSkills}}</dt>
                <dd>
                    <em v-for="item in homeData.skillList">{{item}}</em>
                </dd>
            </dl>
            
            <dl class="icon">
                <dt><i class="iconfont icon-zixuan"></i>{{homeData.skillsDetail}}</dt>
                <dd v-for="item in homeData.skillContent">
                    <h4><i class="iconfont icon-yuan"></i>{{item.title}}</h4>
                    <p>{{item.content}}</p>
                </dd>
            </dl>
            
            <dl class="icon">
                <dt><i class="iconfont icon-mingpian"></i>{{homeData.selfTitle}}</dt>
                <dd v-html="homeData.selfEvaluation"></dd>
            </dl>
        </section>
        <!--  END -->
    </div>
</template>

<script>
    export default {
        mounted(){
            this.fetchData();
        },

        data(){
            return {
                homeData : {}
            }
        },

        methods:{
            fetchData(){
                var _this=this;
                this.$http.get('../src/data/dataIndex.data').then(function(res){
                    _this.homeData = res.data.result;
                },function(err){
                    console.log(':',err);
                });
            }
        }
    }
</script>


